import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

//引入组件
import Home from '../views/Home.vue';
import Welcome from "../components/Welcome";
//用户管理
import Users from "../views/User management/Users";
//权限管理
import Rights from "../views/Rights management/Rights";
import Roles from "../views/Rights management/Roles";
//商品管理
import Params from "../views/Commodity management/Params";
import Goods from "../views/Commodity management/Goods";
import Categories from "../views/Commodity management/Categories";
//订单管理
import Orders from "../views/The order management/Orders";
//数据统计
import Reports from "../views/Data statistics/Reports";

  const routes = [
    {
      path:'/Login',
      name:'Login',
      component:()=>import('../views/Login/Login')
    },
    {
      path:"/",
      redirect:'/Login'
    },
    {
      path:'/HomePage',
      name:'HomePage',
      component:()=>import('../views/Home/HomePage'),
      redirect:'/welcome',
      children:[
        {path:'/welcome',component:Welcome},
        {path:'/users',component:Users},
        {path:'/rights',component:Rights},
        {path:'/roles',component:Roles},
        {path:'/params',component:Params},
        {path:'/goods',component:Goods},
        {path:'/categories',component:Categories},
        {path:'/orders',component:Orders},
        {path:'/reports',component:Reports},
      ]
    },
    // {
    //   path: '/',
    //   name: 'Home',
    //   component: Home
    // },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

//挂载路由导航首位
router.beforeEach((to,form,next)=>{
  //to ---将要访问的路径   //form --- 从哪个路径访问    //   //next 函数 代表放行
  if(to.path === '/Login'){
    return next();
  };
  //获取token
  let tokenStr = window.sessionStorage.getItem('token');
  //判断
  if(!tokenStr){
    return next('/Login');
  }else{
    next();
  }
})

export default router
